import React, { useState }  from 'react';
import ReactDOM from "react-dom";

export default function BasicCocomo() {

    const [kdsi,setkdsi] = useState(0);
    const [project,setproject] = useState("Organic");
    const [effort,seteffort] = useState(0);
    const [tdev,settdev] = useState(0);

    function calculateValue(e){
        e.preventDefault();
        console.log(kdsi,project);
        if(project==="Organic"){
            var a = 2.4 , b = 1.05, c = 0.38;
        }
        else if(project==="Organic"){
             a = 3 ; b = 1.12; c = 0.35;
        }
        else{
             a = 3.6 ; b = 1.2; c = 0.32;
        }
        const em = eval(a* (kdsi**b));
        const t = eval(2.5*(effort**c));
        seteffort(em);
        settdev(t);
        // r();

        const ee = document.getElementById("effort");
        const content = (
            <div>
              <h4>Effort taken:- {effort}</h4>
              <h4>Estimated time:- {tdev}</h4>
            </div>
          );
          
          ReactDOM.render(content, ee);
    }


    // function r(){
    //     const ee = document.getElementById("effort");
    //     const content = (
    //         <div>
    //           <h4>Effort taken:- {effort}</h4>
    //           <h4>Estimated time:- {tdev}</h4>
    //         </div>
    //       );
          
    //       ReactDOM.render(content, ee);
    // }



    

  return (
    <div>
        <br /><br />
        <h1>Basic COCOMO Model</h1>
        <h3>Enter the value of KDSI and set the project type:</h3>
        <form onSubmit={calculateValue}>
            <input type="number" placeholder='Enter value of KDSI' onChange={(e)=>setkdsi(e.target.value)}/>
            <select onChange={(e)=>setproject(e.target.value)}>
                <option value="Organic">Organic</option>
                <option value="Semi">Semi-deteched</option>
                <option value="Embedded">Embedded</option>
            </select><br /><br />
            <button type='submit'>Calculate</button>
        </form>
        <div id="effort"></div>

    </div>
  )
}






